@font-face {
  font-family: "NanumGothic";
  src: url("NanumGothicBold.woff2") format("woff2"), url("NanumGothicBold.woff") format("woff"), url("NanumGothicBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NanumGothic";
  src: url("NanumGothic.woff2") format("woff2"), url("NanumGothic.woff") format("woff"), url("NanumGothic.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NanumGothicExtraBold";
  src: url("NanumGothicExtraBold.woff2") format("woff2"), url("NanumGothicExtraBold.woff") format("woff"), url("NanumGothicExtraBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
:root {
  --sc: #3B3B3B;
  --pc: #2A3140;
  --body-color: #000;
  --gray-bg: #E5E7E9;
  --footer-bg: #00012E;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #2A3140;
  --h1toh6: #2A3140;
  --line-light: #D9D9D9;
  --btn-hover: #020495;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Nanum Gothic", sans-serif;
  line-height: 1.5;
  font-size: 24px;
  color: var(--body-color);
}

* {
  font-family: "Nanum Gothic", sans-serif;
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: 4.8125rem;
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: 1.5rem;
  line-height: 2.125rem;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 1.875rem;
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1.5rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 1.3125rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 1rem;
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
}

a {
  color: var(--pc);
  text-decoration: none;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@media (max-width: 1920px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 104rem;
    padding-inline: 6.25rem;
  }
}
.pc {
  color: var(--pc);
}

.sc {
  color: var(--sc);
}

.cross-puzzle {
  background: #F0F3FA;
  padding-block: 0 1.625rem;
}
.cross-puzzle .puzzle-h2 {
  text-align: center;
  margin-bottom: 0;
  font-weight: var(--font-eb);
}
.cross-puzzle .after-submit {
  display: none;
  text-align: center;
}
.cross-puzzle .after-submit h3 {
  font-size: 2rem;
  line-height: 2.8125rem;
  font-weight: var(--font-eb);
  margin-bottom: 0;
}
.cross-puzzle .after-submit p {
  font-size: 1.3125rem;
  line-height: 2.1875rem;
  margin-bottom: 2.5rem;
}
.cross-puzzle .after-submit h2 {
  font-size: 2rem;
  line-height: 3rem;
  font-weight: var(--font-b);
  margin-bottom: 0;
}
.cross-puzzle .question-answer {
  padding-block: 3.75rem 1.5625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4.375rem;
}
.cross-puzzle .question-answer .puzzle form .grid {
  display: grid;
  grid-template-columns: repeat(17, 1fr);
  grid-template-rows: repeat(13, 1fr);
}
.cross-puzzle .question-answer .puzzle form .grid .cell {
  width: 2rem;
  height: 2rem;
  border: 1px solid #707070;
  position: relative;
}
.cross-puzzle .question-answer .puzzle form .grid .cell span {
  position: absolute;
  top: 2%;
  left: 4%;
  font-size: 0.625rem;
  font-weight: var(--font-b);
}
.cross-puzzle .question-answer .puzzle form .grid .cell input {
  width: 100%;
  height: 100%;
  border: none;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  cursor: pointer;
  border-radius: 0;
  background: #fff;
  outline: 0;
}
.cross-puzzle .question-answer .puzzle form .grid .cell input:focus {
  outline: none;
}
.cross-puzzle .question-answer .puzzle form .grid .brd-top {
  border-top: 0;
}
.cross-puzzle .question-answer .puzzle form .grid .brd-left {
  border-left: 0;
}
.cross-puzzle .question-answer .puzzle form .grid .brd-btm {
  border-bottom: 0;
}
.cross-puzzle .question-answer .puzzle form .grid .brd-right {
  border-right: 0;
}
.cross-puzzle .question-answer .puzzle form .grid .empty-cell {
  width: 2rem;
  height: 2rem;
}
.cross-puzzle .question-answer .question .que ol {
  margin-bottom: 1.875rem;
}
.cross-puzzle .question-answer .question h3 {
  font-size: 1.25rem !important;
  line-height: 2.125rem;
  font-weight: var(--font-eb);
  margin-bottom: 0;
}
.cross-puzzle .question-answer .question ol {
  padding-left: 0;
}
.cross-puzzle .question-answer .question ol li {
  font-size: 1.125rem;
  line-height: 2.125rem;
  color: var(--p-color);
  font-weight: var(--font-b);
  display: flex;
}
.cross-puzzle .question-answer .question ol li span {
  display: none;
}
.cross-puzzle .form-submit {
  display: flex;
  align-items: center;
  justify-content: center;
}
.cross-puzzle .form-submit .submit-btn {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  justify-content: center;
  padding: 0.78125rem 2rem;
  background-color: #407BFF;
  color: #fff;
  text-transform: capitalize;
  font-weight: var(--font-eb);
  font-size: 1.25rem;
  border-radius: 0.5rem;
  cursor: pointer;
  width: fit-content;
  outline: 0;
  border: none;
}
.cross-puzzle .form-submit .submit-btn img {
  width: 1.6875rem;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes lineroad {
  to {
    stroke-dashoffset: 0;
  }
}
